.page-header__row {
  display: flex;
  flex-direction: column;
}

.page-header__logo {
  padding-top: 13px;
  padding-bottom: 10px;
}

.page-header__logo-image {
  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

@media (min-width: $tablet-width) {
  .page-header__row {
    flex-direction: row;
    justify-content: space-between;
  }

  .page-header__logo {
    margin: 0;
    padding-top: 24px;
  }

  .page-header img {
    width: 174px;
    height: auto;
  }
}

@media (min-width: $desktop-width) {
  .page-header__logo {
    padding-top: 55px;
  }

  .page-header img {
    width: 203px;
  }

  .page-header {
    position: relative;
  }

  .page-header--index::after {
    position: absolute;
    width: 50vw;
    height: 696px;
    content: "";
    background-color: #63a73a;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-repeat: no-repeat;
    top: 0;
    right: 0;
    z-index: -1;
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    .page-header--index::after {
      background-image: url("../img/index-background-desktop@2x.jpg");
      background-size: 50vw 696px;
    }
  }
}
